<template>
    <div>
        <el-card class="header">
            <el-form :inline="true" class="headerForm" :model="searchHeaderForm">
                <el-form-item><el-input clearable v-model="searchHeaderForm.find" placeholder="请输入名称"></el-input>
                </el-form-item>
                <el-button type="primary" @click="getDatas">查询</el-button>
                <!-- <el-button class="searchBtn" type="primary" @click="handleData('add')">添 加</el-button> -->
            </el-form>
        </el-card>
        <el-card>
            <el-table :data="tableData" border style="width: 100%"
                :header-cell-style="{ background: '#F3F4F7', color: '#555' }">
                <el-table-column prop="name" label="功能名称" min-width="200">
                </el-table-column>
                <el-table-column prop="type" label="类型" min-width="200">
                </el-table-column>
                <el-table-column prop="group" label="分组" min-width="200">
                </el-table-column>
                <el-table-column align="center" fixed="right" label="操作" width="200">
                    <template #default="scope">
                        <el-button type="primary" v-if="!scope.row.is_used" @click="useFunction(scope.row)">启用</el-button>
                        <el-button type="danger" v-if="scope.row.is_used" @click="dontFunction(scope.row)">停止</el-button>
                        <!-- <el-button type="primary" @click="handleData('update', scope.row)">修改</el-button>
                        <el-button type="danger" @click="deleteData(scope.row._id)">删除</el-button> -->
                    </template>
                </el-table-column>
            </el-table>
            <div v-if="tableData.length !== 0" style="display: flex; flex-direction: row-reverse; margin-top: 10px">
                <el-pagination :current-page="currentPage" :page-size="pageSize" :total="total"
                    :page-sizes="[10, 20, 50, 100]" background layout="total, sizes, prev, pager, next, jumper"
                    @size-change="handleSizeChange" @current-change="handleCurrentChange" />
            </div>
        </el-card>
        <el-dialog :modal-append-to-body="false" v-model="operaDialog" :title="dialogTitle" @close="resetForm">
            <el-form status-icon :model="operaForm" ref="operaForm" class="operaDialog" label-width="100px" :rules="rules">
                <el-form-item label="功能名称" prop="name">
                    <el-input v-model="operaForm.name" />
                </el-form-item>
                <el-form-item label="类型" prop="type">
                    <el-select v-model="operaForm.type">
                        <el-option label="管理员" value="管理员" />
                        <el-option label="教师" value="教师" />
                        <el-option label="学生" value="学生" />
                    </el-select>
                </el-form-item>
                <el-form-item label="分组" prop="group">
                    <el-input v-model="operaForm.group" />
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="submitForm()">提交</el-button>
                    <el-button @click="resetForm">取消</el-button>
                </el-form-item>
            </el-form>

        </el-dialog>
    </div>
</template>
<script>
import {
    getByToken,
    function_create,
    function_delete,
    function_update,
    function_find,
} from '@/api/manager/api.js'

// eslint-disable-next-line no-unused-vars
export default {
    data() {



        return {
            tableData: [],
            // 控制收缩
            isCollapse: false,
            search: "",
            operaDialog: false,
            dialogTitle: "",
            pageSize: 10, // 分页的页面大小
            total: 0, // 分页的数据总数
            currentPage: 1, // 分页的当前页
            opera: "",
            operaForm: {},
            //查询
            searchHeaderForm: {
                find: '',
            },
        }
    },
    created() {
        this.getDatas()
    },
    methods: {

        desearch() {
            this.searchHeaderForm = {
                find: '',
            },
                this.getDatas()
        },
        handleData(handle, row) {
            if (handle == "add") {
                this.operaForm = {}
                this.dialogTitle = "添加API"
                this.opera = "add"
            } else if (handle == "update") {
                this.operaForm = { ...row } //!!! 直接赋值是引用类型,引起表单重置问题
                this.dialogTitle = "编辑API"
                this.opera = "update"
            }
            this.operaDialog = true
        },
        handleSizeChange(value) {
            // 改变页面大小
            this.pageSize = value
            this.getDatas()
        },
        handleCurrentChange(value) {
            // 改变页数
            this.currentPage = value
            this.getDatas()
        },
        async getDatas() {
            const params = {
                page: this.currentPage,
                page_size: this.pageSize,
                search: this.searchHeaderForm.find,
            }
            const res = await function_find(params)
            if (res.data.code === 200) {
                if (res.data.data.data) {
                    this.total = res.data.data.count
                    this.tableData = res.data.data.data
                } else {
                    this.total = res.data.data.count
                    this.tableData = []
                }
            }
        },
        addData() {
            function_create(this.operaForm).then((res) => {
                console.log(res);
                if (res.data.code === 200) {
                    this.$message({
                        showClose: true,
                        message: "创建成功",
                        type: "success",
                    })
                    this.operaDialog = false
                    this.getDatas()
                }
            })
        },
        deleteData(data) {
            function_delete({ _id: data }).then((res) => {
                console.log(data);
                if (res.data.code === 200) {
                    this.$message({
                        showClose: true,
                        message: "删除成功",
                        type: "success",
                    })
                    this.operaDialog = false
                    this.getDatas()
                }
            })
        },
        editData() {
            function_update(this.operaForm).then((res) => {
                if (res.data.code === 200) {
                    this.$message({
                        showClose: true,
                        message: res.data.msg,
                        type: "success",
                    })
                    this.operaDialog = false
                    this.getDatas()
                }
            })
        },
        // <el-button type="primary" v-if="!scope.row.is_used" @click="useFunction(scope.row)">启用</el-button>
        //<el-button type="danger" v-if="scope.row.is_used" @click="dontFunction(scope.row)">停止</el-button>
        useFunction(row) {
            row.is_used = true
            function_update(row).then((res) => {
                if (res.data.code === 200) {
                    this.$message({
                        showClose: true,
                        message: res.data.msg,
                        type: "success",
                    })
                    this.operaDialog = false
                    this.getDatas()
                }
            })
        },
        dontFunction(row) {
            row.is_used = false
            function_update(row).then((res) => {
                if (res.data.code === 200) {
                    this.$message({
                        showClose: true,
                        message: res.data.msg,
                        type: "success",
                    })
                    this.operaDialog = false
                    this.getDatas()
                }
            })
        },
        submitForm() {
            this.$refs.operaForm.validate((valid) => {
                if (valid) {
                    if (this.opera === "add") {
                        this.addData()
                    } else {
                        this.editData()
                    }
                } else {
                    this.$message({
                        showClose: true,
                        message: "请根据要求将信息填写完整",
                        type: "warning",
                    })
                }
            })
        },
        resetForm() {
            this.$refs.operaForm.clearValidate()
            this.operaForm = {},
                this.operaDialog = false
        },
    },
};
</script>
  
<style lang="less" scoped>
.header {
    margin-bottom: 20px;

    .searchBtn {
        float: right;
    }

    .headerForm {
        padding: 0 !important;

        .el-form-item {
            margin-bottom: 0px;
        }
    }
}

.upload-demo {
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
}

.el-date-editor.el-input {
    width: 100%;
}

.fileLink {
    color: #f8f8f8;
    text-decoration: none;
}

.el-form {
    padding: 0 100px;
}

/deep/ .operaDialog .el-form-item__label {
    padding: 0 25px 0 0;
}
</style>
  